import * as React from "react"
import { Link } from "gatsby"

export interface Project {
  title: string,
  Description: React.FC,
  image?: string,
  imageAlt?: string,
}

const ProjectView: React.FC<Project> = ({title, Description, image, imageAlt}: Project) => {
  return (
    <div className="basis-2/5 grow">
      <h2 className="text-2xl">{title}</h2>
      <div className="mt-4"><Description /></div>
      {image != null &&
        <img className="mt-4" src={image} alt={imageAlt} />
      }
    </div>
  )
}  

type Result<T> = {status: 'ok', value: T} | {status: 'error', error: Error}

function aa(): Result<string> {
  return Math.random() > 0.5 ? {status: 'ok', value: 'yay'} : {status: 'error', error: new Error('nope')}
}

export default ProjectView;